var width;
export default {
	onLoad: function(event) {
		var detail = new Object();
		detail = JSON.parse(decodeURIComponent(event.detail))
		
		try {
			this.onload = JSON.parse(decodeURIComponent(event.detail));
		} catch (error) {
			this.onload = JSON.parse(event.detail);
		}
		console.log(this.onload);
		uni.setNavigationBarTitle({
			title: this.onload.date+this.onload.storeName+'空气指数'
		});
		uni.getSystemInfo({
			success(res) {
				width = res.screenWidth - 10;
			}
		})
	},
	data() {
		return {

		}
	},
	onReady: function() {
		//this.hideLoading();
	},
	methods: {	
		goDetail:function(id,storeName){			
			let detail = {
					id: id,
					storeName:storeName,
					date:this.onload.date
				}
				uni.navigateTo({
					url: "dayitem23?detail=" + encodeURIComponent(JSON.stringify(detail))
				})
		}
	}
}
